.usersInRoom{
    padding:0;
}

.user {
    height:50px;
    border-radius: 5px;
    padding-bottom: 2px;
    margin-left:10px;
    padding-left:5px;
    background-color: rgb(130, 167, 236);
    box-shadow: 2px 2px 2px aliceblue;
}

.start {
    text-align: center;
    width: 90%;
    height: 50px;
    border-radius: 10px;
    margin-top: 10px;
    background-color: #FFF;
}

.start:hover {
    background-color: #000;
    color:#FFF;
}